.desktopWrapper{
    font-family: 'Open Sans', sans-serif;
    display: grid;
    height: 100vh;
    z-index: 999;
    width: 100vw;
    grid-template-columns: 1fr 3fr 1fr;
    grid-template-rows: 100px 6fr 1fr 1fr;
    gap: 0px 0px;
    background-color: rgb(34,34,34);
    grid-template-areas:
    ". SearchBar ."
    ". First ."
    ". Tabs ."
    ". . .";
}
  

.mobileWrapper{
    font-family: 'Open Sans', sans-serif;
    display: grid;
    height: 100vh;
    z-index: 999;
    padding-bottom: 20px;
    width: 100vw;
    overflow-x: auto;
    grid-template-columns: 0fr 8fr 0fr;
    grid-template-rows: 100px 6fr 100px;
    background-color: rgb(34,34,34);
    gap: 0px 0px;
    grid-template-areas:
      ". SearchBar ."
      ". Rows ."
      ". Tabs .";

}

.rowWrapper{
    grid-area: Rows;
    width: 100%;
    height: auto;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: rgb(34, 34, 34);
}

.searchBar{
    grid-area: SearchBar;
    background-color: rgb(68, 68, 68);
    border-radius: 10px 10px 0 0;
    height: auto;
}

.searchBarMobile{
    grid-area: SearchBar;
    background-color: rgb(68, 68, 68);
    border-radius: 10px 10px 0 0;
    height: auto;
}


.second{
    grid-area: Second;
}

.tabs{
    display: grid;
    height: 100px;
    grid-area: Tabs;
    background-color: rgb(68, 68, 68);
    border-radius: 0 0 10px 10px;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 2fr;
    gap: 0px 0px;
    grid-template-areas:
      ". . . . .";
}

.tabsMobile{
    display: grid;
    height: 100px;
    grid-area: Tabs;
    background-color: rgb(68, 68, 68);
    border-radius: 0 0 10px 10px;
    grid-template-columns:  1fr 1fr 1fr 1fr ;
    grid-template-rows: 2fr;
    gap: 0px 0px;
    grid-template-areas:
      " . . . ";
}

.searchLabel{
    color: rgb(34, 34, 34);
    font-size: 32px;
}

.searchInput{
    position: relative;
    width: 200px;
    color: rgb(255, 255, 255);
    border-color:rgb(233, 233, 233);
    border-radius: 5px;
    font-size: 28px;
    pointer-events: none;
}

.searchUI{
    position: relative;
    top:10%;
    left: 50px;
}

.tabItem{
    position: relative;
    text-align: center;
    top: 25px;
    font-size: 36px;
}

.selected{
    position: relative;
    background-color: rgb(233, 233, 233);
    width: 60px;
    height: 50px;
    margin: 0 auto;
    border-radius: 10px;
}

.first {
    grid-area: First;
    overflow-y: auto;
    background-color: rgb(233, 233, 233);
    display: grid;
    position: relative;
    padding-top: 20px;
    padding-bottom: 20px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 20px 20px;
    grid-template-areas:
      ". . ."
      ". . ."
      ". . .";
}


.galleryItem{
    position: relative;
    margin: auto;
    width: 15rem;
    height: 15rem;
    background-color: rgb(34,34,34);
}

.rowContainer{
    display: inline;
    overflow-x: auto;
}


.galleryItemFrame{
    display: grid;
    width: 100%;
    height: 500px;
    grid-template-columns: 1fr;
    grid-template-rows: 65px 8fr 1fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
    "Header"
    "Ilustration"
    "Title"
    "More";
}

.itemDescription{
    grid-area: Title;
    position: relative;
    padding-left: 10px;
    color: rgb(34,34,34);
    background-color: rgb(34,34,34);
    
}

.itemShowMore{
    grid-area: More;
    position: relative;
    left: 10px;
    color:rgb(34,34,34);
    background-color:  rgb(34,34,34);

}

.galleryItemMobile {
    grid-area: Ilustration;
    flex-shrink: 0;
    min-width: 80%;
    min-height: 80%;
    overflow-y: hidden;
    display: inline;
    position: relative;
    width: 100%;
    height: auto;
    background-color: rgb(34,34,34);
}

.headerBlock{
    grid-area: Header;
    display: grid;
    grid-template-columns: 1fr 6fr;
    grid-template-rows: 1fr;
    gap: 0px 0px;
    grid-template-areas:
      "Avatar Name"; 
}


.locationLogo{
    grid-area: Avatar;
    position: relative;
    top: 5px;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    border:3px solid #8C52FF ;
}

.accountName{
    position: relative;
    color:  #8C52FF ;
    grid-area: Name;
    font-size: 30px;
    left: 5px;
    top: 15px;
}