.vidgetWrapperDesktop{
    font-family: 'Open Sans', sans-serif;
    position: fixed;
    bottom:50px;
    right: 50px;
    width: 380px;
    height: 150px;
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
      "ChangeText Icon"
      "HotlineLabel .";
      z-index: 999;
}

.desktopWrapper{
    position: absolute;
}

.questionText3{
      position: relative;
      left: -20px;
      font-size: 30px;
      grid-area: ChangeText;
      color: #8C52FF;
      text-shadow: 0 0 5px #8C52FF, 0 0 10px rgb(0, 0, 0), 0 0 15px #8C52FF;
      z-index: 999;
}

