.wrapper{
    overflow-x: hidden;
}

.geometryDecoration{
    position: absolute;
    left: 5%;
    z-index: 1;
    opacity: 0.3;
}

.darkStar{
    z-index: 1;
    width: 30%;
    height: 30%;
}

.geometryDecorationMobile{
    position: absolute;
    left: 60%;
    z-index: 999;
    opacity: 0.3;
}

.darkStarMobile{
    z-index: 999;
    width: 70%;
    height: 70%;
}