
.mobileWrapper {
    width: 100%;
    height: 800px;
    background-color: rgb(34,34,34);
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
    gap: 0px 0px;
    overflow-x: hidden;
    grid-template-areas:
      ". ."
      ". ."
      ". ."
      ". ."
      ". .";
}

.desktopWrapper {
  width: 100%;
  height: auto;
  display: grid;
  background-color: rgb(34,34,34);
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 0px 0px;
  overflow-x: hidden;
  grid-template-areas:
  ". . . ."
  ". . . ."
  ". . . .";
}



.aboutItem{
    width: 100%;
    height: 100%;
    overflow-y: hidden;
}

.aboutItemImg{
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 100%;
    height: 100%;
}

.aboutItemLabel{
    width: 100%;
    height: 100%;
    background-color: rgb(34,34,34);
}

.aboutItemLabelDesktop{
    width: 100%;
    height: 100%;
    background-color: rgb(34,34,34);
}

.aboutLabel{
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 1rem;
    padding-top: 60px;
    color: rgba(255, 255, 255, 0.911);
    text-shadow: 0 0 5px #fff, 0 0 10px rgb(0, 0, 0), 0 0 15px #ffff05;
}

.aboutLabelDesktop{
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 2rem;
    padding-top: 50%;
    color: rgba(248, 222, 146, 1);
    z-index: 999;
    text-shadow: 0 0 5px #fff, 0 0 10px rgb(0, 0, 0), 0 0 15px #ffff05;
}



.galleryImg{
    max-width: 100%;
    height: auto;
    overflow-y: hidden;
}

.galleryImgDesktop{
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%;
    max-width: 100%;
}



.galleryDecoration{
    position:absolute;
    opacity: 0.15;
    width: 20%;
    height: auto;
    transform: rotate(30deg);
    z-index:3;
