.desktopWrapper{
    font-family: 'Open Sans', sans-serif;
    background-color: rgb(34,34,34);
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: 0.7fr 4fr 2fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
      ". buyTicketLabel ."
      ". imgCarousel ."
      ". mainText ."
      ". buyButton .";
}

.geometryDecoration{
    position: absolute;
    left: 10%;
    top: 10%;
}

.geometryDecorationMobile{
    position: absolute;
    left: 10%;
    top: 50%;
}


.geometryDecoration{
    position: absolute;
    left: 5%;
    top: 10%;
}

.geometryDecoration2{
    position: absolute;
    left: 70%;
    top: 20%;
}

.geometryDecoration3{
    position: absolute;
    left: 70%;
    top: 40%;
}

.geometryDecoration4{
    position: absolute;
    left: 30%;
    top: 30%;
}



.darkPentagon{
    z-index: 1;
    width: 70%;
    height: 70%;
    opacity: 0.4;
    transform: rotate(20deg);
}

.darkPentagon2{
    z-index: 1;
    width: 70%;
    height: 70%;
    opacity: 0.4;
}

.darkPentagon3{
    z-index: 1;
    width: 50%;
    height: 50%;
    opacity: 0.4;
}

.darkPentagon4{
    z-index: 1;
    width: 60%;
    height: 60%;
    opacity: 0.4;
}




.mobileWrapper {
    font-family: 'Open Sans', sans-serif;
    overflow-x: hidden;
    background-color: rgb(34,34,34);
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 8fr 1fr;
    grid-template-rows: 1fr 4fr 2fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
    ". buyTicketLabel ."
    ". imgCarousel ."
    ". mainText ."
    ". buyButton .";
}

.buyTicket{
    grid-area: buyTicketLabel;
    z-index: 999;
}

.carouselWrapper{
    grid-area: imgCarousel;
}

.carouselWrapperMobile{
    grid-area: imgCarousel;
}

.mainTextWrapper{
    grid-area: mainText;
    align-items: center;
}

.buttonWrapper{
    grid-area: buyButton;
}

.more{
    grid-area: showMore;
}

.buyTicket{
    text-align: center;
    z-index: 999;
}

.buyTicketLink{
    text-decoration: none;
    color:#F6CB4B;
    font-size: 2rem;
    z-index: 999;
    text-shadow: 0 0 5px #F6CB4B, 0 0 10px rgb(0, 0, 0), 0 0 15px #F6CB4B;
}

.carouselPhotoMobile{
    width: 20rem;
    height: 20rem;
    position: absolute;
    margin: 0 auto;
    z-index: 3;
}


.carouselWrapperMobile{
    margin: 0 auto;
    width: 20rem;
    height: 20rem;
    z-index: 3;
    display: flex;
    overflow: hidden
}

.carouselWrapper{
    margin: 0 auto;
    width: 24vw;
    height: auto;
    z-index: 3;
    display: flex;
    overflow: hidden
}

.carouselPhoto{
    width: 24vw;
    height: auto;
    position: absolute;
    margin: 0 auto;
    z-index: 3;
}

.styledTextWrapper{
    width: 23rem;
    height: 6rem;
    margin: 0 auto;
    padding: 0;
}

.styledTextWrapper2{
    width: 20.5rem;
    height: 4rem;
    margin: 0 auto;
    padding: 0;
}

.styledTextWrapper3{
    width: 22rem;
    height: 4rem;
    margin: 0 auto;
    padding: 0;
}

.ticketButton{
    width: 350px;
    margin:0 auto;
    display:block;
    height: 50px;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 24px;
    border: none;
    background-color: #8C52FF ;
    color: rgb(230, 230, 230);
    text-shadow: 0 0 5px rgb(230, 230, 230), 0 0 10px rgb(0, 0, 0), 0 0 15px rgb(230, 230, 230);
    z-index: 3;
}

.moreLabel{
    font-size: 2rem;
    color:#F6CB4B;
    text-align: center;
}