.desktopWrapper {
    font-family: 'Open Sans', sans-serif;
    display: grid;
    background-color: rgb(34,34,34);
    width: 100vw;
    height: auto;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
      ". first ."
      ". second ."
      ". third .";
}

.geometryDecoration{
    position: absolute;
    left: 20%;
    z-index: 1;
    transform: rotate(40deg);
    opacity: 0.3;
}

.darkStar{
