  .mobileWrapper {
    font-family: 'Open Sans', sans-serif;
    display: grid;
    background-color: rgb(34,34,34);
    width: 100vw;
    height: auto;
    grid-template-columns: 1fr 7fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
    ". first ."
    ". second ."
    ". third .";
}

.firstItem{
    z-index: 3;
    grid-area: first;
    padding-bottom: 10px;
}

.secondItem{
    z-index: 3;
    grid-area: second;
    padding-bottom: 10px;
}

.thirdItem{
    z-index: 3;
    grid-area: third;
}

.title{
    z-index: 3;
    font-size: 1rem;
}

.customLabelDesktop{
    position: relative;
    width: 30rem;
    height: 4rem;
    margin: 0 auto;
    padding: 0;  
}

.customLabelMobile{
    position: relative;
    width: 20rem;
    height: 4rem;
    margin: 0 auto;
    padding: 0;  
}

.description{
    z-index: 999;
    color: whitesmoke;
    font-size: 2rem;
}

.descriptionMobile{
