.desktopWrapper{
    font-family: 'Open Sans', sans-serif;
    width: 100vw;
    height: 100vh;
    display: grid;
    position: relative;
    grid-template-columns: 1fr auto 1fr;
    grid-template-rows: 1fr auto 1fr;
    gap: 0px 0px;
    background-color: rgb(34,34,34);
    grid-template-areas:
    ". . ."
    ". Card ."
    ". . .";

}

.styleMagnifier{
    position: absolute;
    font-size: 10vw;
    top: 15vh;
    left: 25vw;
}

.cardFrame{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    gap: 0px 0px;
    grid-template-areas:
      "Photo";
    background-color: rgb(68, 68, 68);
    grid-area: Card;
    border-radius: 10px;
}

.photoWrapper{
    grid-area: Photo;
}

.photoDescription{
    grid-area: Description;
    display: grid;
    grid-template-columns: 9fr 1fr;
    grid-template-rows: 1fr 90px  4fr;
    gap: 0px 0px;
    grid-template-areas:
      ". Exit"
      "Title ."
      "Text .";
}

.header{
    grid-area: Title;
    position: relative;
    left: 5%;
}

.description{
    grid-area: Text;
    position: relative;
    left: 5%;
    overflow-y: auto; 
    color: whitesmoke
}

.descriptionText{
    font-size: 1.7rem;
}

.exitButton{
    grid-area: Exit;
    align-items: center;
    justify-content: center;
}

.photoWrapper{
    display: grid;
    grid-template-columns: 20px 5fr 20px;
    grid-template-rows: 20px 5fr 20px;
    gap: 10px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    grid-template-areas:
      ". . ."
      ". Image ."
      ". . .";

}

.frame{
    grid-area: Image;
    display: grid;
    grid-template-columns: 20px 7fr 20px;
    grid-template-rows: 20px 7fr 20px;
    gap: 0px 0px;
    grid-template-areas:
        ". . ."
        ". FramedPhoto ."
        ". . .";
}


.image{
    grid-area: FramedPhoto;
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%;
    max-width: 500px;
    max-height: 500px;
}

.crossIcon{
    font-size: 32px;
    position: relative;
    width: 42px;
    margin: 0 auto;
}
